
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import slide from '@/components/Slide/slide'
export default{
components: {
XHeader,Tab,TabItem,InlineLoading,Toast,slide
},
mounted(){
this.initList(true);
},
data() {
return {
tabIndex: 0,
noData: [false, false, false],
type:'1,2,3,4,5',
pageIndex: [1, 1, 1],
pageSize: 15,
list0: [],
list1: [],
list2: [],
test:[],
}
},
methods: {
slide(index){
},
initList(upOrDown) {
if (upOrDown) {
this.pageIndex[this.tabIndex] = 1;
}

if(this.tabIndex == 0){
this.type = '1,2,3,4,5';
}else if(this.tabIndex == 1){
this.type = '1';
}else if(this.tabIndex == 2){
this.type = '2,3,4,5';
}
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
'MSH.1':'myreport',
'MSH.2':'list',
'CHZBM':this.$root.loginInfo.CHZBM,
'num':10000,
'type':this.type,
'pageIndex':this.pageIndex[this.tabIndex],
'pageSize':this.pageSize,
'token':this.$root.loginInfo.token,
}).then(res => {
this.test = res.rows.length;
this.$vux.loading.hide();
if (typeof res != 'undefined' && typeof res.rows != 'undefined') {
if (res.pageNumber == 1) {
this.pageIndex[this.tabIndex] = 1;
switch (this.tabIndex) {
case 0:
this.list0 = [];
break;
case 1:
this.list1 = [];
break;
case 2:
this.list2 = [];
break;
default:
break;
}
}
if (res.rows.length > 0) {
this.noData[this.tabIndex] = false;
this.pageIndex[this.tabIndex]++;
switch (this.tabIndex) {
case 0:
this.list0 = [...this.list0,...res.rows];
break;
case 1:
this.list1 = [...this.list1,...res.rows];
break;
case 2:
this.list2 = [...this.list2,...res.rows];
break;
default:
break;
}
} else {
if (res.pageNumber > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.noData[this.tabIndex] = true;
}
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
});
},
getTitleString(type) {
if (type == 1) {
return "检查检验报告单";
} else if (type == 2) {
return "影像诊断报告单";
} else if (type == 3) {
return "病理诊断报告单";
} else if (type == 4) {
return "心电诊断报告单";
} else if (type == 5) {
return "其他报告单";
} else {
return "";
}
},
reportsDetails(type,id,sjlym,YLJGID) {
if (type == 1) {
this.$router.push({name:'InspectionReport', params:{id:id,sjlym:sjlym,YLJGID:YLJGID}});
} else if (type == 2) {
this.$router.push({name:'ImgReport', params:{id:id,sjlym:sjlym,YLJGID:YLJGID}});
} else if (type == 3) {
this.$router.push({name:'PathologicalReport', params:{id:id,sjlym:sjlym,YLJGID:YLJGID}});
} else if (type == 4) {
this.$router.push({name:'ECGReport', params:{id:id,sjlym:sjlym,YLJGID:YLJGID}});
} else if (type == 5) {
this.$router.push({name:'otherReport', params:{id:id,sjlym:sjlym,YLJGID:YLJGID}});
}
},
isJY(type) {
if (type == 1) {
return "icon1";
} else if (type == 2 || type == 3 || type == 4 || type == 5) {
return "icon2";
} else {
return "";
}
},
},
watch: {
tabIndex: function(newPage,oldPage){
this.$refs.slide.goToPage(newPage);
switch (this.tabIndex) {
case 0:
if (this.list0.length <= 0) {
this.initList(true);
}
break;
case 1:
if (this.list1.length <= 0) {
this.initList(true);
}
break;
case 2:
if (this.list2.length <= 0) {
this.initList(true);
}
break;
default:
break;
}
},
},
}





